<template>
  <div>
    <slot>
      <el-button type="text" @click="initData">设置城市</el-button>
    </slot>
    <el-dialog :visible="visible" title="选择区域" :before-close="close">
      <div style="padding:20px;height:300px;overflow:auto" @click="clearRegion">
        <div class="area-checkbox-group" v-for="item in regionList" :key="item.id" @click.stop="stop">
          <el-checkbox :label="item.areaCode" v-model="checkRegions[item.areaCode]" @change="changeRegion(item)" :class="checkRegionId==item.id?'check-active':''" :indeterminate="checkCitys[item.areaCode]&&checkCitys[item.areaCode].length!=cityArray[item.areaCode].length&&
         checkCitys[item.areaCode].length!==0?true:false">
            {{item.areaName}}
          </el-checkbox>
          <span class="el-icon-caret-bottom show-city" @click.stop="showCity(item)"></span>
          <div class="city__warpper" v-show="checkRegionId==item.id" @click.stop='stop()'>
            <el-checkbox-group v-model="checkCitys[item.areaCode]">
              <el-checkbox v-for="city in cityArray[item.areaCode]" :key="city.id" :label="city.areaCode" @change="changeCity(item.areaCode,city)" class="city-checkbox__inner"> {{city.areaName}}</el-checkbox>
            </el-checkbox-group>
          </div>
        </div>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" size="mini" @click="determine">确 定</el-button>
        <el-button type='info' size="mini" @click="close">取 消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { areaRegionList } from '@/api/setting/area.js'
export default {
  props: {
    index: {
      type: Number,
      default: 1
    },
    data: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      visible: false,
      checkRegionId: '',
      checkRegions: {
        // '11000': false
      },
      cityArray: {
        // 对应省份的城市数组
        // '11000': []
      },
      checkCitys: {
        // '111000':[1,2,3,4,5,6]
      }, // 选中的城市对象
      regionList: [],
      checkData: [
        // {'111000':'南昌'}
      ]
    }
  },
  methods: {
    initData() {
      this.visible = true
      this.getRegionList()
    },
    getRegionList() {
      // 获取省份信息
      areaRegionList().then(res => {
        if (res.code === 200) {
          this.regionList = res.data
        }
      })
    },
    getCity(id, areaCode, status) {
      // 城市的id，城市的编码值，是否默认选中所有地区市
      if (this.cityArray[areaCode] && this.cityArray[areaCode].length > 0) {
        this.checkRegionId = this.checkRegionId === id ? '' : id
      } else {
        areaRegionList({ parentId: id }).then(res => {
          if (res.code === 200) {
            this.$set(this.cityArray, areaCode, res.data)
            if (status) {
              this.$set(
                this.checkCitys,
                areaCode,
                res.data.map(v => v.areaCode)
              )
            } else {
              this.$set(this.checkCitys, areaCode, [])
              this.checkRegionId = id
            }
          }
        })
      }
    },

    changeRegion(item) {
      if (
        this.cityArray[item.areaCode] &&
        this.cityArray[item.areaCode].length > 0
      ) {
        if (this.checkCitys[item.areaCode].length > 0) {
          // 存在就清空，不存在选中
          this.checkCitys[item.areaCode] = []
          this.checkRegions[item.areaCode] = false
        } else {
          this.checkCitys[item.areaCode] = this.cityArray[item.areaCode].map(
            v => v.areaCode
          )
          this.checkRegions[item.areaCode] = true
        }
      } else {
        this.getCity(item.id, item.areaCode, true)
      }
    },
    changeCity(areaCode, city) {
      const checkLen = this.checkCitys[areaCode].length
      const cityLen = this.cityArray[areaCode].length

      if (checkLen === cityLen) {
        this.$set(this.checkRegions, areaCode, true)
      } else {
        this.$set(this.checkRegions, areaCode, false)
      }
    },
    showCity(item) {
      this.getCity(item.id, item.areaCode, false)
      // 显示城市
    },
    clearRegion() {
      this.checkRegionId = ''
    },
    determine() {
      const params = {}
      let cityArray = []
      let checkArray = []
      for (const i in this.cityArray) {
        cityArray = cityArray.concat(this.cityArray[i])
      }
      for (const i in this.checkCitys) {
        checkArray = checkArray.concat(this.checkCitys[i])
      }
      for (const region in this.checkRegions) {
        if (this.checkRegions[region]) {
          for (let i = 0; i < this.regionList.length; i++) {
            if (this.regionList[i].areaCode === region) {
              params[region] = this.regionList[i].areaName
              break
            }
          }
        }
      }
      checkArray.forEach(v => {
        for (let i = 0; i < cityArray.length; i++) {
          if (cityArray[i].areaCode === v) {
            params[cityArray[i].areaCode] = cityArray[i].areaName
            break
          }
        }
      })
      this.$emit('change', params, this.index)
      this.close()
    },
    close() {
      this.visible = false
      this.checkRegionId = ''
    },
    stop() {
      return false
    }
  }
}
</script>

<style <style lang="scss" scoped>
.area-checkbox-group {
  text-align: left;
  float: left;
  width: 160px;
  position: relative;
}
.area-checkbox-group .el-checkbox {
  margin-bottom: 5px;
  margin-left: 0;
}
.city__warpper {
  position: absolute;
  width: 195px;
  height: 200px;
  top: 25px;
  left: 0;
  background: gold;
  z-index: 999;
  overflow: auto;
  padding: 10px;
}
.check-active {
  background: gold;
}
.city-checkbox__inner {
  margin-right: 10px;
}
</style>



