<template>
  <div>
    <cms-breadcrumb to="/freight/list" type='list' @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout" v-loading="loading" element-loading-text="正在操作">
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px" class="the-form">
        <el-form-item label="名称" prop="tpl.expressName" :error='errorMessage.tpl_expressName'>
          <el-input v-model="formData.tpl.expressName"  maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="是否包邮" prop="tpl.isFree" :error='errorMessage.tpl_isFree'>
          <cms-switch v-model="formData.tpl.isFree" @change="changeFree"></cms-switch>
          <span class="gray">切换至不包邮状态下,计价规则将重置</span>
        </el-form-item>
        <el-form-item label="计价方式" prop="tpl.tplType" :error='errorMessage.content' class="default">
          <el-radio-group v-model="formData.tpl.tplType">
            <el-radio :label="1">件数</el-radio>
            <el-radio :label="2">体积</el-radio>
            <el-radio :label="3">重量</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="计价规则" prop="tpl.tplType" :error='errorMessage.content' class="default"
         v-if="status">
          <div class="gray">除指定地区外，其余均采用对应配送方式中的默认规则</div>
        </el-form-item>
        <el-form-item class="the-error" v-if="status"> 
          <post-table :type='3'
           :showType='formData.tpl.tplType' 
           :data='courierData' 
            @change="getCourierData"
            v-show="type.courier" 
            ref="postTable"
          ></post-table>
          <span v-show="errorMsg!=''" class="el-form-item__error">{{errorMsg}}</span>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update()" v-color v-if="$route.query.type=='update'">保存修改</cms-button>
        <cms-button @click="save(false)" v-color v-if="$route.query.type=='save'">保存并新增</cms-button>
        <cms-button @click="save(true)" v-color v-if="$route.query.type=='save'">保存</cms-button>
        <cms-button type='rest' @click="restData($route.query.type)">重置</cms-button>
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import {
  freightSave,
  freightGet,
  freightUpdate
} from '@/api/shop/freight.js'
import postTable from './tpl'
var defaultData = {
  tpl: {
    expressName: '',
    isFree: true,
    tplType: 1
  },
  tplDetails: []
}
export default {
  mixins: [form],
  components: {
    postTable
  },
  data() {
    return {
      status: false,
      formData: Object.assign({}, defaultData),
      type: {
        courier: true
      },
      rules: {
        'tpl.expressName': [rules.required()],
        name: [rules.required()],
        content: [rules.required()]
      },
      courierData: [],
      errorMsg: ''
    }
  },
  methods: {
    restData(type) {
      this.errorMsg = ''
      if (type === 'save') {
        this.rest()
        this.courierData = []
      } else {
        this.getFormData(this.$route.query.id)
      }
    },
    changeFree(data) {
      if (data) {
        this.courierData = []
      }
      this.status = !data
    },
    getCourierData(data) {
      this.courierData = data
    },
    getFormData(id) {
      this.loading = true
      freightGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData.tpl.id = res.data.id
          this.formData.tpl.expressName = res.data.expressName
          this.formData.tpl.isFree = res.data.isFree
          this.formData.tpl.tplType = res.data.tplType
          // 分解数据
          const data = res.data.tplDetails
          data.forEach(v => {
            // if (v.deliveryWay === '3') {
            this.type.courier = true
            this.courierData = v.tplDetails
            // }
          })
          this.loading = false
          this.status = true
        }
      })
    },
    save(state) {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const params = Object.assign({}, this.formData)
          params.tplDetails = params.tplDetails.concat(this.courierData)
          if (params.tpl.isFree) {
            this.saveForm(freightSave, params, state, '/freight/list', 'list')
          } else {
            this.$refs.postTable.validateRequired().then(status => {
              if (status) {
                this.saveForm(freightSave, params, state, '/freight/list', 'list')
              }
            })
          }
        }
      })
    },
    update() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          const params = Object.assign({}, this.formData)
          params.tplDetails = params.tplDetails.concat(this.courierData)
          if (params.tpl.isFree) {
            this.updateForm(freightUpdate, params, '/freight/list', 'list')
          } else {
            this.$refs.postTable.validateRequired().then(status => {
              if (status) {
                this.updateForm(freightUpdate, params, '/freight/list', 'list')
              }
            })
          }
        }
      })
    }
  },
  created() {
    if (this.$route.query.type === 'update') {
      this.getFormData(this.$route.query.id)
    } else {
      this.formData = Object.assign({}, {
        tpl: {
          expressName: '',
          isFree: true,
          tplType: 1
        },
        tplDetails: []
      })
    }
  }
}
</script>
<style lang="scss" >
  .cms-layout .the-form .the-error .el-form-item__error{
    top: 100% !important;
    left: 0 !important;
  }
</style>








