<template>
  <el-form :model="postForm" ref="dynamicValidateForm" :hide-required-asterisk="true">
    <table class="sku-table">
      <tr>
        <td colspan="6" class="align-left demo-form-inline">
          <el-form-item
          label="默认运费"
            prop="postArray.0.firstNum"
            :rules="[{required: true, message: '此项必填', trigger: 'blur'},
            {type: 'number', message: '输入合法数字', trigger: 'blur'}
            ] "
            class="inline-item"
          >
            <el-input type="number" style="width:80px" v-model.number="postForm.postArray[0].firstNum"></el-input>
          </el-form-item>
          <el-form-item
           :label="typeFilter(showType)+'内'"
            prop="postArray.0.firstPrice"
            :rules="{required: true, message: '此项必填', trigger: 'blur'} "
            class="inline-item"
          >
            <el-input  style="width:70px" v-model="postForm.postArray[0].firstPrice"
            type="number"
            ></el-input>
          </el-form-item>
          <el-form-item
            label="元，每增加"
            prop="postArray.0.addNum"
            :rules="{required: true, message: '此项必填', trigger: 'blur'} "
            class="inline-item"
          >
            <el-input  style="width:80px" v-model="postForm.postArray[0].addNum"
            type="number"></el-input>
          </el-form-item>
          <el-form-item
          :label="typeFilter(showType)+'，增加运费'"
            prop="postArray.0.addPrice"
            :rules="{required: true, message: '此项必填', trigger: 'blur'} "
            class="inline-item"
          >
            <el-input maxlength="14" style="width:80px" v-model="postForm.postArray[0].addPrice"
            type="number"
            ></el-input>
            <label>&nbsp;元</label>
          </el-form-item>
        </td>
      </tr>
      <tr>
        <th>地区</th>
        <th>
          首 {{showType|nameFilter}} 数(
          <span>{{showType|typeFilter}}</span> )
        </th>
        <th>首费(元)</th>
        <th>
          续
          <span>{{showType|nameFilter}}</span> 数(
          <span>{{showType|typeFilter}}</span> )
        </th>
        <th>续费(元)</th>
        <th>操作</th>
      </tr>
      <template v-for="(item,index) in postForm.postArray">
        <tr :key="index" v-if="index>0">
          <td style="width:30%">{{showCity(item.districtidList)}}</td>
          <td>
            <el-form-item
              style="width:100%"
              :prop="'postArray.'+index+'.firstNum'"
              :rules="{required: true, message: '此项必填', trigger: 'blur'} "
            >
              <el-input type="number"  v-model="item.firstNum" class="full-width"></el-input>
            </el-form-item>
          </td>
          <td>
            <el-form-item
              style="width:100%"
              :prop="'postArray.'+index+'.firstPrice'"
              :rules="{required: true, message: '此项必填', trigger: 'blur'} "
            >
              <el-input type="number" v-model.number="item.firstPrice" class="full-width"></el-input>
            </el-form-item>
          </td>
          <td>
            <el-form-item
              style="width:100%"
              :prop="'postArray.'+index+'.addNum'"
              :rules="{required: true, message: '此项必填', trigger: 'blur'}"
            >
              <el-input type="number" v-model="item.addNum" class="full-width"></el-input>
            </el-form-item>
          </td>
          <td>
            <el-form-item
              style="width:100%"
              :prop="'postArray.'+index+'.addPrice'"
              :rules=" {required: true, message: '此项必填', trigger: 'blur'}"
            >
              <el-input type="number" v-model="item.addPrice" class="full-width"></el-input>
            </el-form-item>
          </td>
          <td style="width:20%">
            <city-area
              style="display:inline-block"
              @change="getArea"
              :index="index"
              :data="item.districtidList"
            ></city-area>
            <el-button type="text" @click="del(index)">删除</el-button>
          </td>
        </tr>
      </template>
      <tr>
        <td colspan="6" class="align-left">
          <el-button type="primary" size="mini" @click="addPost">为指定城市新增运费</el-button>
        </td>
      </tr>
    </table>
  </el-form>
</template>

<script>
import cityArea from './area'
import rules from '@/rules'
export default {
  props: {
    type: {
      type: [String, Number],
      default: 1
    },
    showType: {
      type: [String, Number],
      default: 1
    },
    data: {
      type: [Array],
      default: function() {
        return []
      }
    }
  },
  components: {
    cityArea
  },
  data() {
    return {
      postForm: {
        postArray: [
          {
            deliveryWay: this.type,
            districtidList: '',
            firstPrice: '',
            firstNum: '',
            addPrice: '',
            addNum: '',
            isDefault: true
          }
        ]
      },
      rules: {
        firstNum: [rules.required(), rules.number()],
        addPrice: [rules.required(), rules.decimal()],
        addNum: [rules.required(), rules.number()],
        firstPrice: [rules.required(), rules.decimal()]
      }
    }
  },
  watch: {
    postForm: {
      handler() {
        this.$emit('change', this.postForm.postArray)
      },
      deep: true
    }
  },
  methods: {
    del(i) {
      this.postForm.postArray.splice(i, 1)
    },
    validateRequired() {
      return new Promise(reslove => {
        this.$refs.dynamicValidateForm.validate(valid => {
          reslove(valid)
        })
      })
    },
    addPost() {
      const obj = {
        deliveryWay: this.type,
        districtidList: '',
        firstPrice: '',
        firstNum: '',
        addPrice: '',
        addNum: '',
        isDefault: false
      }
      this.postForm.postArray.push(obj)
    },
    getArea(data, index) {
      this.$set(
        this.postForm.postArray[index],
        'districtidList',
        JSON.stringify(data)
      )
    },
    showCity(data) {
      if (data === '') {
        return '暂未选择城市'
      } else {
        const obj = JSON.parse(data)
        const arr = []
        for (const key in obj) {
          arr.push(obj[key])
        }
        return arr.toString()
      }
    },
    typeFilter(val) {
      var arr = ['', '件', '立方米', 'kg']
      return arr[parseInt(val)]
    }
  },
  filters: {
    typeFilter(val) {
      var arr = ['', '件', '立方米', 'kg']
      return arr[parseInt(val)]
    },
    nameFilter(val) {
      var arr = ['', '件', '体积', '重']
      return arr[parseInt(val)]
    }
  },
  created() {
    if (this.data.length > 0) {
      this.postForm.postArray = this.$cms.deepCopy(this.data)
    }
  }
}
</script>

 <style lang="scss" scoped>
.align-left {
  text-align: left;
  padding: 10px 30px;
  border-bottom: 1px solid #d7d7d7;
}
.full-width {
  width: 100% !important;
}
td {
  padding: 15px 5px;
}
.inline-item {
  float: left;
  display: flex;
  .el-form-item__label{
    padding: 0 !important;
  }
}
 .cms-layout .the-form .inline-item .el-form-item__error{
    top: 100% !important;
    left: 0 !important;
  }
 
</style>


